@import "../../styles/vars/sizes.scss";

//! parallax
.intro_screen_wrapper {
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
}
.content_wrapper {
    transform-style: preserve-3d;
    transform: translateZ(500px);

    //
    margin-top: calc(100vh - $nav_height);
}
// ! parallax end

.intro_screen_wrapper {
    .canvasWrapper {
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        opacity: 0.5; //!
        transform: translateZ(50px) scale(1.2);

        .canvas {
            width: 100%;
            height: 100%;
            display: block;
        }
    }

    .photo_wrapper {
        width: 100%;
        height: 100%;

        display: block;

        background-color: black;
        background-image: url("./face.jpg");
        background-size: cover;
        background-repeat: no-repeat;
        background-position: 50% 70%;
        background-attachment: fixed;

        // box-shadow: 0 0 8px 8px white inset;

        @media screen and (max-width: 770px) {
            background-position: 50% 100%;
        }
    }

    .intro_screen_content_wrapper {
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        transform: translateZ(70px);

        box-sizing: border-box;
        padding-top: $nav_height;

        .intro_screen_content {
            height: 100%;
            width: 100%;
            box-sizing: border-box;
            padding: 20px;
            padding-bottom: 40px;
            display: flex;
            flex-direction: column;
            justify-content: flex-end;
            align-items: center;

            .header1 {
                color: white;
                text-shadow: 0 0 20px black;
                text-align: center;
            }
            .header2 {
                color: rgba(255, 255, 255, 0.692);
                text-align: center;
                margin: 5px 0;
                margin-bottom: 7.5px;
            }

            .scrollDownBtn {
                outline: none;
                border: none;
                user-select: none;
                display: inline-flex;
                max-width: 90%;
                box-sizing: border-box;
                padding: 20px 40px;
                background: rgb(0, 0, 0);
                color: rgb(0, 117, 0);
                border-radius: 10px;
                margin-top: 1px;
                &:hover {
                    transform: scale(1.1);
                    background: #000;
                    box-shadow: 0 0 5px 2px rgba(0, 0, 0, 0.726);
                }
                &:active {
                    background: rgb(0, 0, 0);

                    transform: scale(0.9);
                }
            }
        }
    }
}

.content_wrapper {
    box-shadow: 0 -20px 20px 2px rgba(0, 0, 0, 0.596);

    background: rgb(114, 122, 0);

    box-sizing: border-box;
    padding: 6px 8px;
    padding-top: 6px;

    .content_top_line {
        margin: 5px auto;
        margin-bottom: 5px;
        display: block;
        background: #000;
        height: 5px;
        width: 50%;
        max-width: 380px;
        border-radius: 10px;
    }
}

.chatLinkWrapper {
    display: flex;
    justify-content: center;
    align-items: center;

    $size: 65px;

    height: $size;
    width: $size;
    position: fixed;
    right: 0;
    margin: 30px;
    bottom: -100%;

    border-radius: 50%;
    background: red;

    transition: 0.6s;

    &.chatLinkActive {
        bottom: 0;
        background: green;
    }
}

.skills_wrapper_main {
    display: flex;
    flex-wrap: wrap;
    max-width: 500px;
    .skill_wrapper {
        margin: 5px;

        // $br: 5px;

        &,
        .skill_name_wrapper,
        .skill_name,
        .skill_description_wrapper {
            border-radius: 5px;
        }
        // overflow: hidden;

        .skill_name_wrapper {
            display: flex;
            justify-content: space-between;
            align-items: center;

            .skill_name {
                display: inline-block;
                background: green;
                padding: 10px;
                white-space: nowrap;
                user-select: none;
                cursor: help;
                user-select: none;

                &:hover {
                    background: rgb(0, 109, 0);
                }
                &:active {
                    background: rgb(0, 139, 0);
                }
            }
            .close_btn {
                display: none;
            }
        }
        .skill_description_wrapper {
            display: none;
        }

        &.skill_description_is_opened {
            display: block;
            width: 100%;
            background: yellow;

            border-top-left-radius: 10px;

            .skill_name {
                // border-radius: 0px;
                border-top-right-radius: 0;
                border-bottom-right-radius: 0;
                border-bottom-left-radius: 0;

                &,
                &:hover,
                &:active {
                    cursor: default;
                    background: rgb(10, 78, 0);
                }
            }

            .close_btn {
                display: inline-flex;
                justify-content: center;
                align-items: center;
                box-sizing: border-box;
                height: 25px;
                width: 25px;
                margin: 6px;
                background: red;
                user-select: none;
                cursor: pointer;
            }

            .skill_description_wrapper {
                display: block;
                width: 100%;
                background: #ffa800d1;
                box-sizing: border-box;
                padding: 10px;
                border-top-left-radius: 0;

                border-top-right-radius: 0;

                .skill_description_text {
                }
            }
        }
    }
}

// TODO DELETE
.header {
    background: red;
    p,
    h3 {
        box-sizing: border-box;
        padding: 20px;
    }
    h3 {
        padding-top: 20px;
        padding-bottom: 0;
    }
    * {
        transform: translateX(-100%) scale(0.5);
        transition: all 0.8s;
    }
    &.active {
        * {
            transform: translateX(0%) scale(1);
        }
    }
}
